.imagePreview {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1em;
    padding: 1em;
    border: 2px dashed #ccc;
    border-radius: 8px;
    background-color: #fff;
  }
  
  .filteredImage {
    max-width: 100%;
    max-height: 400px;
    border-radius: 8px;
    transition: filter 0.3s ease;
  }
  