.filterControls {
    display: flex;
    flex-direction: column;
    margin-top: 1em;
    gap: 0.5em;
  }
  
  select {
    padding: 0.5em;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    background-color: #fff;
    color: #333;
    transition: background-color 0.2s ease;
  }
  
  select:focus {
    outline: none;
    border-color: #0073e6;
    background-color: #f0f8ff;
  }
  